<script>
  console.log("hello create-vc-demo");
  import { CreateDID } from "../../components/create-did";
  import { CreateVC } from "../../components/create-vc";
	import { onMount } from 'svelte';

 import { writable } from "svelte/store";
  const store = writable(localStorage.getItem("user") || "");

  let _user;
  store.subscribe(value => _user = JSON.parse(value));

  let state = { loading: false };
  console.log("User: ");
  console.log( _user);

  onMount(async () => {
    setTimeout(() => {
      console.log("loaded!", state);
      state.loading = false;
      console.log("loaded!", state);
    }, 1000)
	});

    async function test() {
      console.log("user")
      console.log(_user)
    }

</script>

<svelte:options tag="create-vc-demo" />

<div>
  <h1>Demo</h1>

  {#if state.loading}
    loading...
  {:else}
    <pre>{{ _user }}</pre>
  {/if}
    <button on:click={test}>Test</button>
  <CreateDID bind:user={_user} />
  <CreateVC />
</div>
